import React from "react"
import { graphql } from "gatsby"
import MasterTemplate from "../components/MasterTemplate"
import PostLink from "../components/PostLink"
// styles
import "../styles.css"
import { PostsContainer } from "../styles/PostsContainer"

export default function Home({
  data: {
    allMarkdownRemark: { edges }
  }
}) {
  const Posts = edges
    .filter(edge => !!edge.node.frontmatter.isArticle)
    .filter(edge => !!edge.node.frontmatter.date)
    .filter(edge => edge.node.frontmatter.audioURL)
    .map(edge => <PostLink key={edge.node.id} post={edge.node.frontmatter} />)

  return (
    <MasterTemplate>
      <PostsContainer>{Posts}</PostsContainer>
    </MasterTemplate>
  )
}

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            isArticle
            slug
            title
            description
            tag
            audioURL
          }
        }
      }
    }
  }
`
