import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"
import calendarIcon from "../images/iconmonstr-calendar-4.svg"
import timeIcon from "../images/iconmonstr-time-8.svg"
import podcastIcon from "../images/iconmonstr-rss-feed-1.svg"
import { device } from "../styles/variables"

const StyledPost = styled.div`
  background-color: #f9f9f9;
  color: rgba(0, 0, 0, 0.54);
  font-size: 15px;
  margin: 20px 0px;
  padding: 20px 25px;
  position: relative;
  text-align: left;

  @media ${device.tablet} {
    margin: 10px 10px;
    width: 300px;
    padding: 20px 25px;
  }
`

const StyledIcon = styled.img`
  display: inline-block;
  height: 16px;
  margin: 0 0 -2px;
  padding: 0px;
  width: 16px;
`

const StyledDate = styled.span`
  color: #3da2ff;
  line-height: 30px;
  margin: 0;
  padding: 0px 10px;
`

const StyledPodcastIcon = styled.img`
  display: inline-block;
  height: 25px;
  margin: 0 0 -2px;
  padding: 0px;
  position: absolute;
  right: 20px;
  width: 25px;
`

const StyledPostTitle = styled.h2`
  border-radius: 5px;
  color: #222;
  font-size: 21px;
  font-weight: 600;
  letter-spacing: 1px;
  margin: 0;
  margin-right: 5px;
  padding: 5px 0px;

  @media ${device.mobileM} {
    font-size: 25px;
  }
`

const StyledPostTag = styled.span`
  height: 30px;
  line-height: 30px;
  margin: 0;
  margin-right: 5px;
  padding: 5px 0px;
`

const StyledDescription = styled.p`
  min-height: 55px;
`

const StyledReadTime = styled.span`
  color: #eb5c58;
  display: inline-block;
  height: 20px;
  line-height: 20px;
  margin: 0;
  padding: 0px;
`

export default function PostLink({ post }) {
  const { title, slug, tag, date, description, audioURL, readTime } = post
  return (
    <StyledPost>
      <StyledIcon src={calendarIcon} />
      <StyledDate>{date}</StyledDate>
      {audioURL && <StyledPodcastIcon src={podcastIcon} />}
      <Link to={slug}>
        <StyledPostTitle>{title}</StyledPostTitle>
      </Link>
      <StyledDescription>{description}</StyledDescription>
      <StyledReadTime>
        {readTime && (
          <>
            <StyledIcon src={timeIcon} /> {readTime} min read
          </>
        )}
      </StyledReadTime>
      <div>
        <StyledPostTag>{tag}</StyledPostTag>
      </div>
    </StyledPost>
  )
}
